import React from "react";

import {OraButton, OraMessage, OraLoader} from "eshop/components/OraCommonComponents";
import OnlineUtils from "eshop/utils/OnlineUtils";

export default class MsisdnVerificationComponent extends React.Component {

    componentWillMount() {
        this.id =  "msisdn-verification-row" + (this.props.index || this.props.index === 0 ? ("-" + this.props.index): "");
    }

    componentDidMount() {
        if(!this.props.parentId || document.getElementById(this.id)) {
                console.log("Mount-init");
            OPL.UI.loadModules(document.getElementById(this.id), {
                path: 'core/modules/loader',
                options: '{"fadeDuration":120}'
            });

            OPL.UI.loadModules(document.getElementById("msisdnInput"), {
                path: 'common/modules/opl-input-mask',
                options: {mask:'999999999'}
            });
        }
    }

    render() {
        return (
            <div id="msisdn-verification-row" data-js-module="core/modules/loader" >
                <OraLoader className="l-row" loading={this.props.isMsisdnChecking} id="msisdn-verification-loader-component" parentId="msisdn-verification-row" size='m' >

            <div className="opl-console">
               <div className="u-spacing-left-l">

                   <div className="l-row u-spacing-bottom-l">
                       <div className="l-col l-col-small-12 l-col-medium-4 l-col-3">
                           <label htmlFor="msisdnInput">{this.props.inputFieldDesc}</label>
                       </div>
                       <div className="l-col l-col-small-12 l-col-medium-4 l-col-3">
                           <input type="text" id="msisdnInput" role="alert" ref={ref=>this.inputRef=ref}
                                  autoFocus={this.inputRef && !this.inputRef.value && (!OnlineUtils.isMnpApplication(this.props.processType) || this.props.isCustomerSelected)}
                                  defaultValue={this.props.msisdn == "undefined" || !this.props.msisdn ? "" : this.props.msisdn}
                                  onBlur={this.props.handleMsisdnChange}
                                  className={this.props.valid ? '' : 'error'}
                                  />
                           { this.props.valid ? null : <OraMessage type="error" text={this.props.invalidNumberDesc}/>}
                           { this.props.showMsisdnVerificationWarning && <OraMessage type="error" text="Najpierw zweryfikuj numer"/>}
                       </div>
                       <div className="l-col l-col-small-12 l-col-medium-4 l-col-1">
                           <OraButton className="u-w-100 opl-console-button--green" id="msisdn-verification-component-submit-button"
                                      onClick={this.props.validate}
                           >Dalej</OraButton>
                       </div>
                   </div>
               </div>
           </div>
           </OraLoader>
           </div>
        );
    }
};


